<template>
    <div class='registration'>
        <div class='input--icon'>
            <fa-icon class='fa-icon' transform='grow-8' :icon="['fal','user-circle']"/>
            <label for='username__textfield' class='input__label' v-bind:class="(usernameField==='' ? '' : 'input__label--active')">USERNAME</label>
            <input type='text' class='input__textfield' id='username__textfield' v-model='usernameField' placeholder='Username'/>
        </div>
        <div class='input--icon'>
            <fa-icon class='fa-icon' transform='grow-8' :icon="['fal','lock-alt']"/>
            <label for='password__textfield' class='input__label' v-bind:class="(passwordField==='' ? '' : 'input__label--active')">PASSWORD</label>
            <input type='password' id='password__textfield' class='input__textfield' v-model='passwordField' placeholder='Password'/>
        </div>
        <div class='input--icon'>
            <fa-icon class='fa-icon' transform='grow-8' :icon="['fal','check']"/>
            <label for='confirm__textfield' class='input__label' v-bind:class="(confirmField==='' ? '' : 'input__label--active')">CONFIRM PASSWORD</label>
            <input type='password' id='confirm__textfield' class='input__textfield' v-model='confirmField' placeholder='Confirm Password'/>
        </div>
        <input type='button' class='register__btn' value='Register' v-on:click='register'/>
        <div class='register__navigation'>
            <div class='navigation__text'>Already Have An Account?</div>
            <a class='' href='#' v-on:click='mutateContentToLogin' >SIGN IN</a>
        </div>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import axios from 'axios'
axios.defaults.withCredentials = true;
export default {

    data(){
        return{
            usernameField:'',
            passwordField:'',
            confirmField:'',
        }
    },

    methods:{
        ...mapMutations(['SETCONTENTTOLOGIN']),

        mutateContentToLogin: function(){
            console.log("TEST")
            this.SETCONTENTTOLOGIN()
        },

        isEmpty : function(value){
            return (value === null || value === '')
        },

        //DELETE THIS. Needs to be changed to a handler later on instead of being in here.
        register: function(){
            const path = window.location

            if(this.isEmpty(this.usernameField) || this.isEmpty(this.passwordField) || this.isEmpty(this.confirmField)){
                console.log('You forgot something!')
                return null
            }

            if(this.passwordField === (this.confirmField)){
                console.log('Passwords dont match!')
            }

            axios.post('/register',{
                username: this.usernameField,
                password:this.passwordField,
            }).then(response=>{
                if(response.status == 200){
                    console.log('successful registeration')
                }
            }).catch(error=>{
                console.log(error.response.status)
            })
        }
    }
}
</script>

<style lang='scss' scoped>
    @import '../scss/_entryContent'

    

</style>
