<template>
    <div class='dashboard'>
        <div class='toolbar'>
            <img class='toolbar__logo' src='../assets/logo-horizontal.png'/>
            <div class='toolbar__title'>SESSION CONFIGURATION</div>
        </div>
        <!--<dashboard-session/>-->
        <div class='content'>
            <dashboard-configuration class='form'/>   
            <div class='wiki'></div>
            <div class='feedback__mass'></div>
            <div class='feedback__power'></div>
        </div>
    </div>
</template>

<script>
import Configuration from '@/components/DashboardConfiguration'
import Session from '@/components/DashboardSession'
import axios from 'axios'
axios.defaults.withCredentials = true;
export default {

    components:{
        'dashboard-configuration':Configuration,
        'dashboard-session':Session,
    },

    data(){
        return{
            baselineModel:'',

            //DELETE THIS!
            water: "",
            co2:"",
            oxygen:"",
            updateRunning:true,
            updateIntervalID:'',
            updateInterval:1000,
            currentStep:0
        }
    },

     mounted: function(){
        this.setUpdateTimeout()
        console.log("TESTING")
     },

     methods:{
         getStep:function(){             
             const stepSize = 1
             const path= 'http://localhost:8000/get_step'

            if(this.currentStep != null){
                this.currentStep += stepSize
                this.path += '?step_num=' + this.currentStep;                
            }

            axios.get('http://localhost:8000/get_step?step_num='+this.currentStep)
            .then(response =>{
                this.message = 'New Game Started'
                this.oxygen = response.data.agents.total_production.atmo_h2o
                this.co2 = response.data.agents.avg_carbon_dioxide_pressure
                this.water = response.data.agents.total_water
                console.log(response.data)
                //this.setUpdateTimeout()
             })
         },

        setUpdateTimeout:function(){
            this.updateIntervalID = setTimeout(() => {
                if(this.updateRunning){
                    this.getStep()
                }
            },1000)
        }
     }

}
</script>

<style lang="scss" scoped>
    @import '../scss/_theme';

    .dashboard{
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: minmax(192px, 15vw) 1fr;
        grid-column-gap: 32px;
        grid-template-rows: 1fr;
        grid-template-areas:'dashboard__toolbar dashboard__content';
    }

    .toolbar{
        @extend %display-panel;
        display: flex;
        flex-direction:column;
        justify-content:flex-start;
        align-items: center;
    }

    .toolbar__logo{
        width: 100%;
        height: auto;
    }

    .toolbar__title{
        @extend %font-sub-title;
        margin: 16px auto;
        text-align: center;
    }

    .toolbar__title::after{
        display:block;
        margin: 8px 0px;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;
        content:'';
    }

    .content{
        display: grid;
        grid-template-columns: 1.5fr .75fr .75fr;
        grid-template-rows: 2fr 1fr;
        grid-column-gap: 8px;
        grid-row-gap: 8px;
        grid-template-areas:
        'content__configuration content__wiki content__wiki'
        'content__configuration content__mass content__power';

        .form{            
            grid-area:content__configuration;
        }

        .wiki{
            @extend %display-panel;
            grid-area:content__wiki;
        }

        .feedback__mass{
            @extend %display-panel;
            grid-area:content__mass;
        }

        .feedback__power{
            @extend %display-panel;
            grid-area:content__power;
        }
    }

</style>
