<template>
    <div class='configuration'>
        <div class='configuration__title'>
            Initial Settings 
        </div>
        <div class='configuration__section'>
            <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='baselineModel' :label="'BASELINE MODEL'"/>
            <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='location' :label="'LOCATION'"/>      
            <div class='section--inline'>
                <custom-toggle v-model='permanentStay' :label="'PERMANENT STAY'"/>
                <!-- Need to add in display option on both of these if permanent stay is selected-->
                <custom-integer v-model='durationStay' :label="'DURATION'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
                <custom-select :options="[{'value':'durationHour', 'text':'Hours'},{'value':'durationYear', 'text':'Years'}]" v-model='timeType' :label="'TIME'"/>
            </div>
        </div>
        <div class='configuration__title'>
            Astronauts
        </div>
        <div class='configuration__section'>
            <custom-integer v-model='astronauts' :label="'ASTRONAUTS'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
            <custom-integer v-model='foodMass' :label="'FOOD'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
            <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='crewQuarters' :label="'CREW QUARTERS'"/>
            <custom-integer v-model='eclss' :label="'LIFE SUPPORT'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
        </div>
        <div class='configuration__title'>
            Greenhouse
        </div>
        <div class='configuration__section'>
            <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='greenhouse' :label="'GREENHOUSE'"/>
            <div class='section--inline'>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='selectedSeed[0].type' :label="'SEED TYPE'"/>
                <custom-integer v-model='selectedSeed[0].qty' :label="'QTY'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='selectedSeed[1].type' :label="'SEED TYPE'"/>
                <custom-integer v-model='selectedSeed[1].qty' :label="'QTY'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
            </div>
            <div class='section--inline'>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='selectedSeed[2].type' :label="'SEED TYPE'"/>
                <custom-integer v-model='selectedSeed[2].qty' :label="'QTY'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='selectedSeed[3].type' :label="'SEED TYPE'"/>
                <custom-integer v-model='selectedSeed[3].qty' :label="'QTY'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
            </div>
            <div class='section--inline'>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='selectedSeed[4].type' :label="'SEED TYPE'"/>
                <custom-integer v-model='selectedSeed[4].qty' :label="'QTY'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='selectedSeed[5].type' :label="'SEED TYPE'"/>
                <custom-integer v-model='selectedSeed[5].qty' :label="'QTY'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
            </div>
        </div>
        <div class='configuration__title'>
            Power
        </div>
        <div class='configuration__section'>            
            <div class='section--inline'>
                <custom-select :options="[{'value':'bmOne', 'text':'Baseline Model One'}]" v-model='powerSource' :label="'SOURCE TYPE'"/>
                <custom-integer v-model='solarRacks' :label="'STORAGE'" :maxValue="500" style='justify-self:flex-end;margin-top:auto'/>
            </div>
        </div>
        <input type='button' onclick="location.href = '/dashboard'" value='Launch Configuration' class='configuration__submitbtn'>
    </div>
</template>

<script>
import Select from '@/components/InputSelect'
import Integer from '@/components/InputInteger'
import Toggle from '@/components/InputToggle'
import Session from '@/components/DashboardSession'
export default {

    components:{
        'custom-select':Select,
        'custom-integer':Integer,
        'custom-toggle':Toggle,
        'dashboard-session':Session,
    },

    data(){
        return{
            baselineModel:String,
            location:String,
            permanentStay:false,
            durationStay:Number,
            timeType:String,
            astronauts:Number,
            foodMass:Number,
            crewQuarters:Number,
            eclss:Number,
            powerSource:String,
            greenhouse:String,
            solarRacks:Number,
            selectedSeed:[
                {'type':'','qty':0},
                {'type':'','qty':0},
                {'type':'','qty':0},
                {'type':'','qty':0},
                {'type':'','qty':0},
                {'type':'','qty':0},
            ],
          
        }
    }

}
</script>

<style lang="scss" scoped>
    @import '../scss/_theme';
    .configuration{
        @extend %display-panel;
        display: flex;
        flex-direction:column;
    }

    .section--inline{
        display:flex;
        justify-content: flex-start;        
        *{
            margin-right: 8px;
        }
    }

    .section--grid{
        display:grid;
        grid-template-columns: repeat(auto-fit,minmax(0px,auto));
        grid-column-gap: 8px;
        box-sizing: border-box;
    }

    .configuration__title{
        @extend %font-panel-title;
        margin:auto auto auto 0px;
    }

    .configuration__title--initial{
        margin-top: 0px;
        grid-area: initial;
    }

    .configuration__title::after{
        display:block;
        margin: 8px 0px;
        border-bottom: $border-standard-width $border-standard-style $border-standard-color;
        content:'';
    }

    .configuration__section{
        padding-left: 16px;
    }

    .configuration__submitbtn{
        margin: 0px auto;
        margin-top: auto;
        border: 2px solid grey;
        background-color: transparent;
        width: 100%;
        font-size: 1rem;
        padding: 8px;
        border-radius: 10px;
        box-sizing: border-box;
        color: grey;
        font-weight: 600;
        width: auto;
        transition: background-color .2s ease-in-out;
    }

    .configuration__submitbtn--active{

    }

    .configuration__submitbtn--active:hover{
        background-color:#ea3b3b;
    }

</style>
